<template>
	<view>
		<kt-nav-bar title="贡献记录"></kt-nav-bar>
		<user-popup 
		ref="userPopup"
		></user-popup>
		<view>
			<gift-effect ref="giftEffect"></gift-effect>
			<scroll-view scroll-y style="
			height: calc(100vh - 100rpx);
			padding: 20rpx 20rpx 0 20rpx;
			box-sizing: border-box;
			" @scrolltolower="getBodyData()">

				<!-- bodyData -->
				<view 
				style="
				padding: 20rpx 0 20rpx 0;
				border-bottom: 1rpx solid #eee;
				"
				
				v-for="(item, index) in bodyData.content">
					<view>
						<user-card
						@click="$refs.userPopup.open(item.toUser)"
						 :ovoUserInfo="item.toUser"></user-card>
					</view>

					<view class="bottom-text">
						<view v-if="item.typeCode == 'gift'" style="display: inline-block;"
							@click="openGiftEffect(giftMap[item.giftCode])">
							<gift type="contributionValue" 
							:giftNum="item.giftNum"
							:isShowNumber="true" :item="giftMap[item.giftCode]"></gift>
						</view>
					</view>
					<view
					style="font-size: 24rpx;color: #999999;margin-top: 10rpx;"
					>
					<text>
						{{ $kt.util.readableDate(item.gmtCreate) }}
					</text>
					<text
					v-if="item.typeCode == 'gift'"
					style="float: right;"
					>
					赠送了礼物，贡献值+{{ giftMap[item.giftCode].charmValue*item.giftNum }}
					</text>
					</view>

				</view>

			</scroll-view>
		</view>

	</view>
</template>

<script>
export default {
	data() {
		return {
			// 是否已经到底
			isEnd: false,

			giftMap: {},
			bodyData: {
				conetnt: []
			},
			requestParam: {
				pageNumber: 1,
			},
			requestCount: 0,
		}
	},
	created() {
		// 从缓存中获取
		let charmDetailSelf = uni.getStorageSync("getContributionListOfSelf");
		if (charmDetailSelf) {
			this.bodyData.content = charmDetailSelf;
		}

		this.getGiftMap();
		this.getBodyData();
	},

	methods: {
		openGiftEffect(item) {
			console.log(item);
			this.$refs.giftEffect.open(item);
		},

		getBodyData() {
			if (this.isEnd) {
				console.log("已经到底了");
				return null;
			}

			this.$kt.request.request({
				api: this.$api.business.ovo.userCharmDetail.getContributionListOfSelf,
				data: this.requestParam,
				stateSuccess: (res) => {
					console.log(res, "res");
					if (this.requestCount == 0) {
						this.bodyData.content = [];
					}

					this.requestCount++;
					if (res.data.content.length == 0) {
						this.isEnd = true;
						return false;
					}


					// this.bodyData = this.bodyData.concat(res.data);
					for (let i = 0; i < res.data.content.length; i++) {
						this.bodyData.content.push(res.data.content[i]);
					}

					if (this.bodyData.content.length < 100) {
						// 加到缓存中
						uni.setStorageSync("getContributionListOfSelf", this.bodyData.content);
					}

					if (res.data.content.length < res.data.size) {
						this.isEnd = true;
						return false;
					}

					this.requestParam.pageNumber++;
				}
			})
		},
		getGiftMap() {
			this.$kt.request.request({
				api: this.$api.business.ovo.gift.getMap,
				stateSuccess: (res) => {
					this.giftMap = res.data;
				}
			})
		}
	}
}
</script>

<style lang="scss" scoped>
.bottom-text {
	padding: 5rpx 20rpx;
	text-align: center;
}
</style>

